:root,
[data-bs-theme="light"] {
  // Note: Custom variable values only support SassScript inside `#{}`.

  // Colors
  //
  // Generate palettes for full colors, grays, and theme colors.

  @each $color, $value in $colors {
    --#{$prefi***REMOVED***}#{$color}: #{$value};
  }

  @each $color, $value in $grays {
    --#{$prefi***REMOVED***}gray-#{$color}: #{$value};
  }

  @each $color, $value in $theme-colors {
    --#{$prefi***REMOVED***}#{$color}: #{$value};
  }

  @each $color, $value in $theme-colors-rgb {
    --#{$prefi***REMOVED***}#{$color}-rgb: #{$value};
  }

  @each $color, $value in $theme-colors-te***REMOVED***t {
    --#{$prefi***REMOVED***}#{$color}-te***REMOVED***t-emphasis: #{$value};
  }

  @each $color, $value in $theme-colors-bg-subtle {
    --#{$prefi***REMOVED***}#{$color}-bg-subtle: #{$value};
  }

  @each $color, $value in $theme-colors-border-subtle {
    --#{$prefi***REMOVED***}#{$color}-border-subtle: #{$value};
  }

  --#{$prefi***REMOVED***}white-rgb: #{to-rgb($white)};
  --#{$prefi***REMOVED***}black-rgb: #{to-rgb($black)};

  // Fonts

  // Note: Use `inspect` for lists so that quoted items keep the quotes.
  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172
  --#{$prefi***REMOVED***}font-sans-serif: #{inspect($font-family-sans-serif)};
  --#{$prefi***REMOVED***}font-monospace: #{inspect($font-family-monospace)};
  --#{$prefi***REMOVED***}gradient: #{$gradient};

  // Root and body
  // scss-docs-start root-body-variables
  @if $font-size-root != null {
    --#{$prefi***REMOVED***}root-font-size: #{$font-size-root};
  }
  --#{$prefi***REMOVED***}body-font-family: #{inspect($font-family-base)};
  @include rfs($font-size-base, --#{$prefi***REMOVED***}body-font-size);
  --#{$prefi***REMOVED***}body-font-weight: #{$font-weight-base};
  --#{$prefi***REMOVED***}body-line-height: #{$line-height-base};
  @if $body-te***REMOVED***t-align != null {
    --#{$prefi***REMOVED***}body-te***REMOVED***t-align: #{$body-te***REMOVED***t-align};
  }

  --#{$prefi***REMOVED***}body-color: #{$body-color};
  --#{$prefi***REMOVED***}body-color-rgb: #{to-rgb($body-color)};
  --#{$prefi***REMOVED***}body-bg: #{$body-bg};
  --#{$prefi***REMOVED***}body-bg-rgb: #{to-rgb($body-bg)};

  --#{$prefi***REMOVED***}emphasis-color: #{$body-emphasis-color};
  --#{$prefi***REMOVED***}emphasis-color-rgb: #{to-rgb($body-emphasis-color)};

  --#{$prefi***REMOVED***}secondary-color: #{$body-secondary-color};
  --#{$prefi***REMOVED***}secondary-color-rgb: #{to-rgb($body-secondary-color)};
  --#{$prefi***REMOVED***}secondary-bg: #{$body-secondary-bg};
  --#{$prefi***REMOVED***}secondary-bg-rgb: #{to-rgb($body-secondary-bg)};

  --#{$prefi***REMOVED***}tertiary-color: #{$body-tertiary-color};
  --#{$prefi***REMOVED***}tertiary-color-rgb: #{to-rgb($body-tertiary-color)};
  --#{$prefi***REMOVED***}tertiary-bg: #{$body-tertiary-bg};
  --#{$prefi***REMOVED***}tertiary-bg-rgb: #{to-rgb($body-tertiary-bg)};
  // scss-docs-end root-body-variables

  --#{$prefi***REMOVED***}heading-color: #{$headings-color};

  --#{$prefi***REMOVED***}link-color: #{$link-color};
  --#{$prefi***REMOVED***}link-color-rgb: #{to-rgb($link-color)};
  --#{$prefi***REMOVED***}link-decoration: #{$link-decoration};

  --#{$prefi***REMOVED***}link-hover-color: #{$link-hover-color};
  --#{$prefi***REMOVED***}link-hover-color-rgb: #{to-rgb($link-hover-color)};

  @if $link-hover-decoration != null {
    --#{$prefi***REMOVED***}link-hover-decoration: #{$link-hover-decoration};
  }

  --#{$prefi***REMOVED***}code-color: #{$code-color};
  --#{$prefi***REMOVED***}highlight-color: #{$mark-color};
  --#{$prefi***REMOVED***}highlight-bg: #{$mark-bg};

  // scss-docs-start root-border-var
  --#{$prefi***REMOVED***}border-width: #{$border-width};
  --#{$prefi***REMOVED***}border-style: #{$border-style};
  --#{$prefi***REMOVED***}border-color: #{$border-color};
  --#{$prefi***REMOVED***}border-color-translucent: #{$border-color-translucent};

  --#{$prefi***REMOVED***}border-radius: #{$border-radius};
  --#{$prefi***REMOVED***}border-radius-sm: #{$border-radius-sm};
  --#{$prefi***REMOVED***}border-radius-lg: #{$border-radius-lg};
  --#{$prefi***REMOVED***}border-radius-***REMOVED***l: #{$border-radius-***REMOVED***l};
  --#{$prefi***REMOVED***}border-radius-***REMOVED******REMOVED***l: #{$border-radius-***REMOVED******REMOVED***l};
  --#{$prefi***REMOVED***}border-radius-2***REMOVED***l: var(--#{$prefi***REMOVED***}border-radius-***REMOVED******REMOVED***l); // Deprecated in v5.3.0 for consistency
  --#{$prefi***REMOVED***}border-radius-pill: #{$border-radius-pill};
  // scss-docs-end root-border-var

  --#{$prefi***REMOVED***}bo***REMOVED***-shadow: #{$bo***REMOVED***-shadow};
  --#{$prefi***REMOVED***}bo***REMOVED***-shadow-sm: #{$bo***REMOVED***-shadow-sm};
  --#{$prefi***REMOVED***}bo***REMOVED***-shadow-lg: #{$bo***REMOVED***-shadow-lg};
  --#{$prefi***REMOVED***}bo***REMOVED***-shadow-inset: #{$bo***REMOVED***-shadow-inset};

  // Focus styles
  // scss-docs-start root-focus-variables
  --#{$prefi***REMOVED***}focus-ring-width: #{$focus-ring-width};
  --#{$prefi***REMOVED***}focus-ring-opacity: #{$focus-ring-opacity};
  --#{$prefi***REMOVED***}focus-ring-color: #{$focus-ring-color};
  // scss-docs-end root-focus-variables

  // scss-docs-start root-form-validation-variables
  --#{$prefi***REMOVED***}form-valid-color: #{$form-valid-color};
  --#{$prefi***REMOVED***}form-valid-border-color: #{$form-valid-border-color};
  --#{$prefi***REMOVED***}form-invalid-color: #{$form-invalid-color};
  --#{$prefi***REMOVED***}form-invalid-border-color: #{$form-invalid-border-color};
  // scss-docs-end root-form-validation-variables
}

@if $enable-dark-mode {
  @include color-mode(dark, true) {
    color-scheme: dark;

    // scss-docs-start root-dark-mode-vars
    --#{$prefi***REMOVED***}body-color: #{$body-color-dark};
    --#{$prefi***REMOVED***}body-color-rgb: #{to-rgb($body-color-dark)};
    --#{$prefi***REMOVED***}body-bg: #{$body-bg-dark};
    --#{$prefi***REMOVED***}body-bg-rgb: #{to-rgb($body-bg-dark)};

    --#{$prefi***REMOVED***}emphasis-color: #{$body-emphasis-color-dark};
    --#{$prefi***REMOVED***}emphasis-color-rgb: #{to-rgb($body-emphasis-color-dark)};

    --#{$prefi***REMOVED***}secondary-color: #{$body-secondary-color-dark};
    --#{$prefi***REMOVED***}secondary-color-rgb: #{to-rgb($body-secondary-color-dark)};
    --#{$prefi***REMOVED***}secondary-bg: #{$body-secondary-bg-dark};
    --#{$prefi***REMOVED***}secondary-bg-rgb: #{to-rgb($body-secondary-bg-dark)};

    --#{$prefi***REMOVED***}tertiary-color: #{$body-tertiary-color-dark};
    --#{$prefi***REMOVED***}tertiary-color-rgb: #{to-rgb($body-tertiary-color-dark)};
    --#{$prefi***REMOVED***}tertiary-bg: #{$body-tertiary-bg-dark};
    --#{$prefi***REMOVED***}tertiary-bg-rgb: #{to-rgb($body-tertiary-bg-dark)};

    @each $color, $value in $theme-colors-te***REMOVED***t-dark {
      --#{$prefi***REMOVED***}#{$color}-te***REMOVED***t-emphasis: #{$value};
    }

    @each $color, $value in $theme-colors-bg-subtle-dark {
      --#{$prefi***REMOVED***}#{$color}-bg-subtle: #{$value};
    }

    @each $color, $value in $theme-colors-border-subtle-dark {
      --#{$prefi***REMOVED***}#{$color}-border-subtle: #{$value};
    }

    --#{$prefi***REMOVED***}heading-color: #{$headings-color-dark};

    --#{$prefi***REMOVED***}link-color: #{$link-color-dark};
    --#{$prefi***REMOVED***}link-hover-color: #{$link-hover-color-dark};
    --#{$prefi***REMOVED***}link-color-rgb: #{to-rgb($link-color-dark)};
    --#{$prefi***REMOVED***}link-hover-color-rgb: #{to-rgb($link-hover-color-dark)};

    --#{$prefi***REMOVED***}code-color: #{$code-color-dark};
    --#{$prefi***REMOVED***}highlight-color: #{$mark-color-dark};
    --#{$prefi***REMOVED***}highlight-bg: #{$mark-bg-dark};

    --#{$prefi***REMOVED***}border-color: #{$border-color-dark};
    --#{$prefi***REMOVED***}border-color-translucent: #{$border-color-translucent-dark};

    --#{$prefi***REMOVED***}form-valid-color: #{$form-valid-color-dark};
    --#{$prefi***REMOVED***}form-valid-border-color: #{$form-valid-border-color-dark};
    --#{$prefi***REMOVED***}form-invalid-color: #{$form-invalid-color-dark};
    --#{$prefi***REMOVED***}form-invalid-border-color: #{$form-invalid-border-color-dark};
    // scss-docs-end root-dark-mode-vars
  }
}
